  let universeGeometry = new THREE.SphereGeometry(500, 100, 100);
  let universeMaterial = new THREE.MeshLambertMaterial({
    //
    map: new THREE.TextureLoader().load(universeImg),
    side: THREE.DoubleSide, //
  });
  //
