//
function createSpot() {
  let img = new Image();
  img.src = earthGrayscale; //
  //
  img.onload = () => {
    let canvas = document.createElement("canvas");
    canvas.width = img.width; //canvas
    canvas.height = img.height;
    (canvas.getContext("2d") as any).drawImage(img, 0, 0, img.width, img.height);//canvas
    let canData = (canvas.getContext("2d") as any).getImageData(0, 0, canvas.width, canvas.height);//
    let globeCloudBufferGeometry = new THREE.BufferGeometry(); //
    let globeCloudVerticesArray = []; //
    let o = null; //
    for (o = 0; o < canData.data.length; o += 4) {
      let r = (o / 4) % canvas.width,
        i = (o / 4 - r) / canvas.width;
      if ((o / 4) % 2 == 1 && i % 2 == 1)
        if (0 === canData.data[o]) {
          let n = r,
            longitude = (i / (canvas.height / 180) - 90) / -1, //
            latitude = n / (canvas.width / 360) - 180; //
          let s = latLongToVector3(longitude, latitude, earthSize, .1); //
          globeCloudVerticesArray.push(s); //
        }
    }
    let l = new Float32Array(3 * globeCloudVerticesArray.length); //
    for (o = 0; o < globeCloudVerticesArray.length; o++) {
      l[3 * o] = globeCloudVerticesArray[o].x;//
      l[3 * o + 1] = globeCloudVerticesArray[o].y;
      l[3 * o + 2] = globeCloudVerticesArray[o].z;
    }
    let positionVal = new THREE.BufferAttribute(l, 3); //
    globeCloudBufferGeometry.setAttribute("position", positionVal); //
    let globeCloudMaterial = new THREE.PointsMaterial({
      color: new THREE.Color(dvColor[1]),//
      fog: true,
      size: 1,
    });//
    let d = new Float32Array(3 * globeCloudVerticesArray.length), c = [];
    for (o = 0; o < globeCloudVerticesArray.length; o++) {
      c[o] = new THREE.Color(dvColor[1]);//
      d[3 * o] = c[o].r;//rgb
      d[3 * o + 1] = c[o].g;
      d[3 * o + 2] = c[o].b;
    }
    let color_val = new THREE.BufferAttribute(d, 3);
    globeCloudBufferGeometry.setAttribute("color", color_val);//,globeCloudBufferGeometrysetAttribute
    let globeCloud = new THREE.Points(//
      globeCloudBufferGeometry,
      globeCloudMaterial
    );
    globeCloud.name = "globeCloud";
    earthGroup.add(globeCloud); //
  };
};

//e:ato
function latLongToVector3(e: any, a: any, t: any, o: any) {
  var r = (e * Math.PI) / 180,
    i = ((a - 180) * Math.PI) / 180,
    n = -(t + o) * Math.cos(r) * Math.cos(i),
    s = (t + o) * Math.sin(r),
    l = (t + o) * Math.cos(r) * Math.sin(i);
  return new THREE.Vector3(n, s, l); //
};

//
function createVirus(data: any, earthSize: any) {
  let colors = [
    new THREE.Color(0xf9b8b8),
    new THREE.Color(0xfe4242),
    new THREE.Color(0xff0000),
  ]; //
  let virSize = 4; //
  let list = JSON.parse(JSON.stringify(data));
  list.forEach((e: { value: number; color: any; position: any[]; }) => {
    e.value >= 10000000 && (e.color = colors[2]); //
    e.value >= 500000 && e.value < 10000000 && (e.color = colors[1]);
    e.value < 500000 && (e.color = colors[0]);
    if (e.position) {
      let virusMaterial = new THREE.SpriteMaterial({
        color: e.color,
        map: new THREE.TextureLoader().load(virusImg),
        side: THREE.FrontSide, //
      }); //
      let Sprite = new THREE.Sprite(virusMaterial); //
      Sprite.scale.set(virSize, virSize, 1); //
      let lat = e.position[1]; //
      let lon = e.position[0]; //
      let s = latLongToVector3(lat, lon, earthSize, 1); //
      Sprite.position.set(s.x, s.y, s.z); //
      Sprite.dotData = e; //dotData
      Sprite.name = "";
      earthGroup.add(Sprite); //
    }
  });
};

//
function createOrbitControls() {
  orbitControls = new OrbitControls(camera, renderer.domElement);
  orbitControls.enablePan = false; //
  orbitControls.enableZoom = true; //
  orbitControls.enableDamping = true; //
  orbitControls.dampingFactor = 0.05; //(.25)
  orbitControls.minDistance = 250; //
  orbitControls.maxDistance = 400; //
  orbitControls.autoRotate = true; //()
  orbitControls.autoRotateSpeed = 1; //
  orbitControls.enableRotate = true;//
};

//
function render() {
  anId.value = requestAnimationFrame(render);
  document.getElementById("sphereDiv") &&
    document
      .getElementById("sphereDiv")!
      .addEventListener("mousemove", onMousemove, false);
  orbitControls.update(); //
  renderer.render(scene, camera);
};

//(vuedata)
function onMousemove(e: any) {
  let dom = document.getElementById("sphereDiv");
  let width = dom!.clientWidth; //
  let height = dom!.clientHeight; //
  //threejs
  mouse.x = (e.offsetX / width) * 2 - 1;
  mouse.y = -(e.offsetY / height) * 2 + 1;
  raycaster.setFromCamera(mouse, camera); //raycaster
  let intersects = raycaster.intersectObjects(scene.children); // raycaster
  if (intersects.length !== 0 && intersects[0].object.name == "") {
    (isTag) && (currentPointData.value = intersects[0].object.dotData); //intersects
    dom!.style.cursor = "pointer"; //
    position.value = {
      x: e.pageX + 20 + "px",
      y: e.pageY + "px",
    }; //
  } else {
    currentPointData.value = {}; //
    dom!.style.cursor = "move"; //
  }
};

//
function initEchart() {
  let sortList: any = sortFun(sphereData.value);//
  histogramChartFun(sortList.slice(0, sliceNum)); //
  certain.value = Number(allData.value.othertotal.certain);//
  addcure.value = Number(allData.value.othertotal.recure);///
  addDie.value = Number(allData.value.othertotal.die);///
}

//()
function sortFun(arr: any) {
  arr.forEach((a: any, index: number) => {
    for (let j = 0; j < arr.length - 1 - index; j++) {
      if (Number(arr[j].value) < Number(arr[j + 1].value)) {
        let tmp = arr[j];
        arr[j] = arr[j + 1];
        arr[j + 1] = tmp;
      }
    }
  });
  return arr;
};

//
function histogramChartFun(list: any) {
  let chartDom = document.getElementById("histogramDiv");
  (histogramChart) && (histogramChart.dispose());//
  histogramChart = echarts.init(chartDom);
  let option: any = {
    backgroundColor: "",
    title: {
      left: "center",
      top: "3%",
      textStyle: {
        color: "#fff",
      },
    },
    grid: {
      top: "0%",
      left: "25%",
      right: "5%",
      bottom: "0%",
    },
    xAxis: {
      type: "value",
      show: false,
    },
    yAxis: {
      type: "category",
      axisLabel: {
        color: "#fff",
      },
      data: [],
    },
    tooltip: {
      trigger: "axis",
      axisPointer: {
        type: "shadow",
      },
      backgroundColor: "rgba(0,0,0,.5)",
      textStyle: {
        color: "#fff",
        fontWeight: "bolder"
      },
      borderWidth: "0",
    },
    series: [
      {
        data: [],
        type: "bar",
        showBackground: true,
        backgroundStyle: {
          color: "rgb(197, 178, 255,.2)",
        },
        itemStyle: {
          color: "#7b52f7",
        },
        label: {
          color: "#fff",
          fontWeight: "bolder",
          show: true,
          align: "left",
          formatter: "{c}",
        },
      },
    ],
  };
  list.forEach((l: any) => {
    option.yAxis.data.push(l.name);
    option.series[0].data.push(l.value);
  });
  option.yAxis.data.reverse();
  option.series[0].data.reverse();
  option && histogramChart.setOption(option);
}

//ip
function getLocationMsg() {
