};

//
function getProvinceData() {
  let pro = userMsg.value.pro;//
  let ePro = "";//
  //
  if (process.env.NODE_ENV !== "development") {
    //
    allData.value.list.forEach((l: any) => {
      if (pro.search(l.name) >= 0) {
        ePro = l.ename;
      }
    })
    //jsonp
    jsonp1(
      process.env.VUE_APP_5,
      (res: any) => {
        currentProvinceData.value = res.data;//
      },
      "val1",
      "mod=province&province=" + ePro
    );
  } else {
    currentProvinceData.value = tempProvinceData.data;
  }
}

//
function provinceAnalyze() {
  isProvinceEchartDrawer.value = true;
}

//
function sureDownloadReport() {
  if (confirm("")) {
    downloadReport();//
  }
};

//
async function downloadReport() {
  isLoading.value = true;
  let tempData: any = {};//
  await (tempData = JSON.parse(JSON.stringify(currentProvinceData.value)));
  let currentDate: any = currentProvinceData.value.cachetime.split(" ")[0];//
  let wordData: any = {
    wordName: "",//
    hasCityData: true,//
    currentDate: currentDate,//
    version: version.value,//
    currentCityData: {},//
    overviewData: {
      name: tempData.province,
      contotal: tempData.contotal,//
      econNum: tempData.econNum,//
      curetotal: tempData.curetotal,//
      deathtotal: tempData.deathtotal,//
      conadd: tempData.conadd,//
    },
    tabData: [],//
    sourceUrl: window.location.href//url
  };
  await tempData.city.forEach((c: any, index: any) => {
    c.index = index + 1;//
    wordData.tabData.push(c);//
    if (userMsg.value.city.search(c.name) !== -1) {
      wordData.hasCityData = true;
      wordData.currentCityData = c;//
      wordData.wordName = c.name;//
    }
  })
  isLoading.value = false;
  //
  if (!wordData.wordName) {
    wordData.hasCityData = false;
    wordData.wordName = wordData.overviewData.name;
  }
  // console.log(wordData);
  await exportWord("docx/word.docx", wordData, wordData.wordName + ".docx");
  ElMessage({
    type: 'success',
    message: '',
  })
}


